html, body, #root {
    height: 100%;
    background-color: #3A3A3C;
}

.no-decoration{
  text-decoration: none;
}

h3 {margin: 10px 0 5px;}

#header{
  font-size: 1.17em;
  font-weight: bolder;
  display: flex;
  align-items: center;
  justify-content: space-between;

  p{
    margin: 0em;
  }
}

#search-button{
  background-color: #dddddd;
  border: 2px solid #333333;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  width: 12.5%;
  height: 35px;
}

img {
  image-rendering: optimizeSpeed;
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: optimize-contrast;
  image-rendering: pixelated;
  -ms-interpolation-mode: nearest-neighbor;
  vertical-align: middle;
}

.tolerance {
  -webkit-appearance: none;  
  -moz-appearance: none;  
       appearance: none;
  height: 2px ;
  margin: auto;
  background: #fff;
 
}

.tolerance::-webkit-slider-thumb {
  background: #fff;
  cursor: pointer;
}

.tolerance::-moz-range-thumb {
  background: #fff;
  cursor: pointer;
}

.float-right{
  float: right;
}

.tolerance {
  background: #FFF;
  
}

.grid-container{

  justify-content: center;   
  display: grid;
  grid-template-columns: repeat(auto-fill, 128px);
  grid-gap: 8px;
  gap: 8px;
  grid-auto-flow: dense;
}

.fit{
  object-fit: none;
}

.filter-box {
  display: inline-flex;
  padding: 4px 4px;
  background-color: #dddddd;
  border: 2px solid #333333;
  border-radius: 2px;
  margin: 5px 10px 5px 0px;
}

.art-grid{
  background-color: #464646;
  text-align: center;
  position: relative;
  display: inline;
  height:100%;
  min-height: 128px;
}

.grid-w2{
  grid-column-start: span 2;
}

.grid-h2{
  grid-row-end: span 2;
}

.helper {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.overlay{
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.175);
  visibility: hidden;
  
}

.color-count {
  color: whitesmoke !important;
  top: 0px;
  left: 0px;
  position: absolute;
  font-size: smaller;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.5) 30%, rgba(0, 0, 0, 0) 100%);
  visibility: hidden;
}

.launch {
  top: 2px;
  right: 2px;
  position: absolute;
  visibility: hidden;
  
}

.art-grid:hover  .preview, 
.art-grid:hover .launch, 
.art-grid:hover .color-count,
.art-grid:hover .overlay
{
  visibility: visible;
  
}

.preview {
  color: whitesmoke !important;
  bottom: 0px;
  position: absolute;
  font-size: smaller;
  padding-top: 20px;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.4) 30%, rgba(0, 0, 0, 0) 100%);
  visibility: hidden;
  width: 100%;
  text-align: center;
  
}

.search-container{
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}

.search-labels{
  margin-right: 1em;
}

.search-labels, .search-inputs{
